const findKey = require('lodash/findKey');
const get = require('lodash/get');

// http://detectmobilebrowsers.com/
const MOBILE = [
  /(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i,
  /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i,
];

const BROWSER = {
  messenger: [/\bFB[\w_]+\/(Messenger|MESSENGER)/],
  facebook: [/\bFB[\w_]+\//],
  twitter: [/\bTwitter/i],
  line: [/\bLine\//i],
  wechat: [/\bMicroMessenger\//i],
  puffin: [/\bPuffin/i],
  miui: [/\bMiuiBrowser\//i],
  instagram: [/\bInstagram/i],
  chrome: [/\bCrMo\b|CriOS|Android.*Chrome\/[.0-9]* (Mobile)?/],
  safari: [/Version.*Mobile.*Safari|Safari.*Mobile|MobileSafari/],
  ie: [/IEMobile|MSIEMobile/],
  firefox: [/fennec|firefox.*maemo|(Mobile|Tablet).*Firefox|Firefox.*Mobile|FxiOS/],
};

const OS = {
  android: [/Android/],
  ios: [/\biPhone.*Mobile|\biPod|\biPad/],
  blackberry: [/blackberry|\bBB10\b|rim tablet os/],
  palm: [/PalmOS|avantgo|blazer|elaine|hiptop|palm|plucker|xiino/],
  symbian: [/Symbian|SymbOS|Series60|Series40|SYB-[0-9]+|\bS60\b/],
  windows: [
    /Windows CE.*(PPC|Smartphone|Mobile|[0-9]{3}x[0-9]{3})|Window Mobile|Windows Phone [0-9.]+|WCE;/,
    /Windows Phone 10.0|Windows Phone 8.1|Windows Phone 8.0|Windows Phone OS|XBLWP7|ZuneWP7|Windows NT 6.[23]; ARM;/,
  ],
};

const BOT = [
  /Googlebot|facebookexternalhit|AdsBot-Google|Google Keyword Suggestion|Facebot|YandexBot|YandexMobileBot|bingbot|ia_archiver|AhrefsBot|Ezooms|GSLFbot|WBSearchBot|Twitterbot|TweetmemeBot|Twikle|PaperLiBot|Wotbox|UnwindFetchor|Exabot|MJ12bot|YandexImages|TurnitinBot|Pingdom/,
];

class InApp {

  ua = '';

  constructor(useragent) {
    this.ua = useragent;
  }

  get os(): string {
    return findKey(OS, regexs => findKey(regexs, regex => regex.test(this.ua))) || 'other';
  }

  get device(): string {
    return 'other';
  }

  get browser(): string {
    return findKey(BROWSER, regexs => findKey(regexs, regex => regex.test(this.ua))) || 'other';
  }

  isMobile(): boolean {
    return MOBILE[0].test(this.ua) || MOBILE[1].test(this.ua.substr(0, 4)) || false;
  }

  isTablet(): boolean {
    return false;
  }

  isDesktop(): boolean {
    return !this.isMobile();
  }

  inBot(): boolean {
    return BOT.test(this.ua) || false;
  }

  isInApp(): boolean {
    return ['chrome', 'safari', 'ie', 'firefox', 'other'].indexOf(this.browser) < 0;
  }

  isApplePay(): boolean {
    return false;
  }

  open(apps: Object): Promise<boolean> {
    if (document) {
      return new Promise((resolve) => {
        const iframe = document.createElement('iframe');
        let app;
        let timeout;

        switch (this.os) {
          case 'ios':
            app = get(apps, 'ios', {});
            if (app.store) {
              timeout = setTimeout(() => {
                window.location.href = app.store;
                resolve(false);
              }, 1500);
            }

            iframe.onload = () => {
              clearTimeout(timeout);
              iframe.parentNode.removeChild(iframe);
              resolve(true);
              window.location.href = app.uri;
            };

            iframe.src = app.uri;
            iframe.setAttribute('style', 'display:none;');
            document.body.appendChild(iframe);
            break;
          case 'android':
          default:
            resolve(false);
        }
      });
    }

    return Promise.resolve(false);
  }

  addMatch(name, regex): InApp {
    this[name] = () => regex.test(this.ua);
  }
}

module.exports = InApp;



